const Rating = (props) => {
  const { rating, reviewsCount, green } = props;
  return (
    <div className="rating">
      <span style={green ? { color: "rgb(156, 216, 156)" } : {}}>
        <i
          className={
            rating >= 1
              ? "fas fa-star"
              : rating >= 0.5
              ? "fas fa-star-half-alt"
              : "far fa-star"
          }
        />
      </span>
      <span style={green ? { color: "rgb(156, 216, 156)" } : {}}>
        <i
          className={
            rating >= 2
              ? "fas fa-star"
              : rating >= 1.5
              ? "fas fa-star-half-alt"
              : "far fa-star"
          }
        />
      </span>
      <span style={green ? { color: "rgb(156, 216, 156)" } : {}}>
        <i
          className={
            rating >= 3
              ? "fas fa-star"
              : rating >= 2.5
              ? "fas fa-star-half-alt"
              : "far fa-star"
          }
        />
      </span>
      <span style={green ? { color: "rgb(156, 216, 156)" } : {}}>
        <i
          className={
            rating >= 4
              ? "fas fa-star"
              : rating >= 3.5
              ? "fas fa-star-half-alt"
              : "far fa-star"
          }
        />
      </span>
      <span style={green ? { color: "rgb(156, 216, 156)" } : {}}>
        <i
          className={
            rating >= 5
              ? "fas fa-star"
              : rating >= 4.5
              ? "fas fa-star-half-alt"
              : "far fa-star"
          }
        />
      </span>
      <br></br>
      <span style={green ? { color: "rgb(156, 216, 156)" } : {}}>
        {reviewsCount} reviews
      </span>
    </div>
  );
};

export default Rating;
